export default{
  data() {
    return {
      selectData: '全部',
      tabList: ['全部', '待付款', '待收货', '待评价', '已完成', '已取消'],
      scrollLeft: 0,
      dataList: [{
        store: '苹果官方旗舰店',
        sum: '2',
        status: '已取消',
        price: 300,
        arr: [{
          imgUrl: '',
          title: 'Sony/索尼 MDR-ZX310头戴式监听重低耳耳',
          info: '黑色 官方标配'
        }]
      }, {
        store: '香蕉官方旗舰店',
        sum: '2',
        status: '已完成',
        price: 200,
        arr: [{
          imgUrl: '',
          title: 'Sony/索尼 MDR-ZX310头戴式监听重低耳耳',
          info: '黑色 官方标配'
        }, {
          imgUrl: '',
          title: 'Sony/索尼 超级头盔',
          info: '黑色 官方标配'
        }]
      }]
      // dataList: ''        dataList初始值需要为空字符串
    }
  },
  watch: {
    // 监听选中的订单类别，改变位置
    selectData(val) {
      if (val === '已取消' || val === '已完成') {
        this.scrollLeft = 69
        return
      }
      if (val === '全部' || val === '待付款') {
        this.scrollLeft = 0
        return
      }
    }
  },
  onLoad(option) {
    if(option.name) {
      this.selectData = option.name
    }
    console.log('分享文章详情页接受到的参数',option.name)
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    selectTab(item) {
      this.selectData = item
    },
    goDetail(item) {
      uni.navigateTo({
        url: 'orderDetail?item=' + JSON.stringify(item)
      })
    }
  }
}
